<template>
  <div
    @click="rate()"
    :class="{ 'bg-red-200': rated }"
    :style="`cursor: ${rated ? 'normal' : 'pointer'}`"
  >   
   <strong class="block text-black" style="text-align: center">
      {{ count > 0 ? count : " " }}
    </strong>
    <svg
      class="h-5 w-5"
      enable-background="new 0 0 24 24"
      viewBox="0 0 24 24"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        d="m22.25 1h-2.5c-.965 0-1.75.785-1.75 1.75v11.5c0 .965.785 1.75 1.75 1.75h2.5c.965 0 1.75-.785 1.75-1.75v-11.5c0-.965-.785-1.75-1.75-1.75z"
      />
      <path
        d="m5.119.75c-1.95 0-3.61 1.4-3.94 3.32l-1.12 6.5c-.42 2.45 1.46 4.68 3.94 4.68h4.72s-.75 1.5-.75 4c0 3 2.25 4 3.25 4s1.5-.5 1.5-3c0-2.376 2.301-4.288 3.781-5.273v-12.388c-1.601-.741-4.806-1.839-9.781-1.839z"
      />
    </svg>
  </div>
</template>

<script>
import api from "../plugins/feathers-client";
export default {
  name: "rate",
  props: ["videoId"],
  async created() {
    let res = await api.service("api/rates-ms/dislikes").find({
      query: {
        user_id: this.user.id,
        video_id: this.videoId,
        $limit: 0
      }
    });
    this.rated = res.total > 0 ? true : false;
    res = await api.service("api/rates-ms/dislikes").find({
      query: {
        video_id: this.videoId,
        $limit: 0
      }
    });
    this.count = res.total;
  },
  data() {
    return {
      count:0,
      rated: false
    };
  },
  methods: {
    async rate() {
      if (!this.rated) {
        try {
          await api.service("api/rates-ms/dislikes").create({
            video_id: this.videoId,
            user_id: this.user.id
          });
          this.rated = true;
        } catch (exp) {}
      }
    }
  },
  computed: {
    user() {
      return this.$store.state.auth.user;
    }
  }
};
</script>
